.container {
  text-align: center;
  margin: 200px auto;
}
.upper-part {
  margin-bottom: 50px;
  line-height: 2;
}
h2 {
  color: black;
  font-family: "Montserrat", sans-serif;
}
h4 {
  color: black;
  font-family: "Montserrat", sans-serif;
}
.btn {
  background: #71abd1;
  background-image: -webkit-linear-gradient(top, #71abd1, #4f86a8);
  background-image: -moz-linear-gradient(top, #71abd1, #4f86a8);
  background-image: -ms-linear-gradient(top, #71abd1, #4f86a8);
  background-image: -o-linear-gradient(top, #71abd1, #4f86a8);
  background-image: linear-gradient(to bottom, #71abd1, #4f86a8);
  -webkit-border-radius: 28;
  -moz-border-radius: 28;
  border-radius: 28px;
  font-family: Arial;
  color: #ffffff;
  font-size: 20px;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
}

.btn:hover {
  background: #3cb0fd;
  background-image: -webkit-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -moz-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -ms-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -o-linear-gradient(top, #3cb0fd, #3498db);
  background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
  text-decoration: none;
}
